}

.header {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: fixed;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.navbar__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.navbar__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  white-space: nowrap;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.navbar__list li {
  margin: auto 0;
}

.navbar__list li:not(:first-child) {
  margin-left: 20px;
}

.navbar__link:hover, a.is-active {
  color: #00acdc;
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
